import Link from 'next/link'
import Head from 'next/head'
import React from 'react'
import { connect } from 'react-redux'
import fire from '../config/firebaseConfig'
import {Nav, Navbar, NavItem, NavDropdown, Form, FormControl, Button} from 'react-bootstrap'


class Header extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isUserDropdown: false
        }

        this.toggleUserDropDown = this.toggleUserDropDown.bind(this);
    }

    toggleUserDropDown() {
        this.setState({isUserDropdown: !this.state.isUserDropdown});
    }

    logout = () => {
        fire.auth().signOut(
        ).then(
            () => {
                this.props.dispatch({ type: 'LOGOUT' });
            }).catch(
            (err) => {
                console.log("error on logout.", err)
            });
    }

    render() {

        let pages;
        if (!this.props.userData.isManager) {
            let atNum = this.props.assignedTasks.length;
            let trNum = this.props.taskReports.length;
            pages = <ul className="navbar-nav mr-auto">
            <li className="nav-item">
                <Link href="/"><a className="nav-link">שיבוץ לאיסופים</a></Link>
            </li>
            <li className="nav-item">
                <Link href="/assigned-tasks"><a className="nav-link">איסופים קרובים {atNum > 0 ? (<span
                className="badge badge-secondary">{atNum}</span>) : ''}</a></Link>
            </li>
            <li className="nav-item">
                <Link href="/task-reports"><a className="nav-link">מילוי משוב {trNum > 0 ? (<span
                className="badge badge-secondary">{trNum}</span>) : ''}</a></Link>
            </li>
            
        </ul>;
        } else {
            pages = <ul className="navbar-nav mr-auto">
            <li className="nav-item">
            <Link href="/"><a className="nav-link">לוח בקרה</a></Link>
            </li>
            <li className="nav-item">
                <Link href="/users"><a className="nav-link">משתמשים</a></Link>
            </li>
            <li className="nav-item">
                <Link href="/tasks"><a className="nav-link">משימות</a></Link>
            </li>
        </ul>
        }
            return (
                <div>
                    <Head>
                        <meta charset="utf-8" />
                        <meta name="viewport" content="width=device-width, initial-scale=1, shrink-to-fit=no" />
                        <link rel="stylesheet" href="https://cdn.rtlcss.com/bootstrap/v4.2.1/css/bootstrap.min.css"
                            integrity="sha384-vus3nQHTD+5mpDiZ4rkEPlnkcyTP+49BhJ4wJeJunw06ZAp+wzzeBPUXr42fi8If" crossorigin="anonymous" />
                        <link rel="stylesheet" href="https://use.fontawesome.com/releases/v5.8.1/css/all.css"
                            integrity="sha384-50oBUHEmvpQ+1lW4y57PTFmhCaXp0ML5d60M1M7uH2+nqUivzIebhndOJK28anvf" crossorigin="anonymous" />
                        <title>LeketGo</title>
                    </Head>
                    <Navbar sticky="top" bg='dark' variant='dark'>
                        <Navbar.Brand href="/">LetsGo</Navbar.Brand>
                        <Navbar.Toggle aria-controls="basic-navbar-nav" />
                        <Navbar.Collapse id="basic-navbar-nav">
                            <Nav className="mr-auto">
                            <Nav.Link href="/">לוח בקרה</Nav.Link>
                            <Nav.Link href="/users">משתמשים</Nav.Link>
                            </Nav>
                            <Nav className='mr-sm-2'>
                            <NavDropdown alignRight title={this.props.userData.firstName + ' ' + this.props.userData.lastName} id="basic-nav-dropdown">
                                <NavDropdown.Item href="/personal-information">פרטים אישיים</NavDropdown.Item>
                                <NavDropdown.Divider />
                                <NavDropdown.Item href="/login">התנתקות</NavDropdown.Item>
                            </NavDropdown>
                            </Nav>
                        </Navbar.Collapse>
                        </Navbar>
                    {/* <nav className="navbar navbar-expand-lg navbar-dark bg-dark position-sticky sticky-top">
                        <Link href="/"><a className="navbar-brand">LeketGo</a></Link>
                        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent"
                            aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="navbar-toggler-icon"></span>
                        </button>
                        <div className="collapse navbar-collapse" id="navbarSupportedContent">
                            {pages}
                            <ul className="navbar-nav">
                                <li className={"nav-item dropdown"}>
                                    <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" onClick={this.toggleUserDropDown}>
                                        {this.props.userData.firstName} {this.props.userData.lastName}
                                    </a>
                                    <div className={'dropdown-menu dropdown-menu-right' + (this.state.isUserDropdown ? ' show' : '')} aria-labelledby="navbarDropdown">
                                        <Link href="/personal-information"><a className="dropdown-item">פרטים אישיים</a></Link>
                                        <div className="dropdown-divider"></div>
                                        <Link href="/login"><a className="dropdown-item" onClick={this.logout}>התנתקות</a></Link>
                                    </div>
                                </li>
                                <li className="nav-item">
                                    <span className="nav-link"><i className="fas fa-map-marker-alt"></i> {this.props.userData.region}</span>
                                </li>
                            </ul>
                        </div>
                    </nav> */}
                </div>
            );
    }
}

export default connect(state => state)(Header);